class PCMRenderer {

    constructor(option) {
        this.TAG = 'PCMRenderer';
        this.option = null;
        this.samples = null;
        this.flush = null;
        this.flushInterval = null;
        this.maxValue = 0;
        this.typedArray = Float32Array;
        this.audioCtx = null;
        this.gainNode = null;
        this.startTime = 0;
        this.volume_ = 1.0;
        this.muted_ = false;

        this.init(option);
    }

    init(option) {
        var defaults = {
            encoding: '32bitFloat',
            channels: 2,
        sampleRate: 44100,
        flushingTime: 1000,
        touchResume:true
        };
        this.option = Object.assign({}, defaults, option);
        this.samples = new Float32Array();
        // this.flush = this.flush.bind(this);
        this.flushInterval = setInterval(this.flush, this.option.flushingTime);
        this.maxValue = this.getMaxValue();
        this.typedArray = this.getTypedArray();
        this.createContext();
    };
    
    getMaxValue() {
        var encodings = {
            '8bitInt': 128,
            '16bitInt': 32768,
            '32bitInt': 2147483648,
            '32bitFloat': 1
        }
    
        return encodings[this.option.encoding] ? encodings[this.option.encoding] : encodings['16bitInt'];
    };
    
    getTypedArray() {
        var typedArrays = {
            '8bitInt': Int8Array,
            '16bitInt': Int16Array,
            '32bitInt': Int32Array,
            '32bitFloat': Float32Array
        }
    
        return typedArrays[this.option.encoding] ? typedArrays[this.option.encoding] : typedArrays['16bitInt'];
    };
    
    createContext() {
        this.audioCtx = new (window.AudioContext || window.webkitAudioContext)();
    
        // create gain node, it can control audio volume
        this.gainNode = this.audioCtx.createGain();
        this.gainNode.gain.value = this.volume_;
        this.gainNode.connect(this.audioCtx.destination);
        this.startTime = this.audioCtx.currentTime; // type: double, unit: second
    if (this.option.touchResume) {
        if (this.audioCtx.state === 'suspended' && 'ontouchstart' in window) {
            const unlock = () => {
                this.audioCtx.resume();
                this.volume = this.volume_;
                document.body.removeEventListener('touchstart', unlock, false);
                document.body.removeEventListener('click', unlock, false);
            };
            document.body.addEventListener('touchstart', unlock, false);
            document.body.addEventListener('click', unlock, false);
        }
    }
  };

    isTypedArray(data) {
        return (data.byteLength && data.buffer && data.buffer.constructor == ArrayBuffer);
    };
    
    feed(data) {
        if (!this.isTypedArray(data)) return;
        data = this.getFormatedValue(data);
        var tmp = new Float32Array(this.samples.length + data.length);
        tmp.set(this.samples, 0);
        tmp.set(data, this.samples.length);
        this.samples = tmp;
    };
    
    getFormatedValue(data) {
        var data = new this.typedArray(data.buffer),
            float32 = new Float32Array(data.length),
            i;
    
        for (i = 0; i < data.length; i++) {
            float32[i] = data[i] / this.maxValue;
        }
        return float32;
    };
    
    set volume(volume) {
        this.volume_ = volume;
        if(volume <= 0) {
            this.muted_ = true;
            this.volume_ = 0;
        } 
        else{
            this.muted_ = false;
        }
        if(this.gainNode) {
            this.gainNode.gain.value = this.volume_;
        }  
    };
    get volume() {
        if(this.muted_) {
            return 0;
        }
        else {
            return this.volume_;
        }
    }
    get muted() {
        return this.muted_;
    }
    set muted(muted) {
        if(this.muted_ != muted) {
            this.muted_ = muted;
            if(this.muted_) {
                if(this.gainNode) {
                    this.gainNode.gain.value = 0;
                }  
            } 
            else{
                if(this.gainNode) {
                    this.gainNode.gain.value = this.volume_;
                }  
            }
        }
    }

    destroy() {
        if (this.flushInterval) {
            clearInterval(this.flushInterval);
        }
        this.samples = null;
        this.audioCtx.close();
        this.audioCtx = null;
    };
    
    flush() {
        if (!this.samples.length) return;
        var bufferSource = this.audioCtx.createBufferSource(),
            length = this.samples.length / this.option.channels,
            audioBuffer = this.audioCtx.createBuffer(this.option.channels, length, this.option.sampleRate),
            audioData,
            channel,
            offset,
            i,
            decrement;
    
        for (channel = 0; channel < this.option.channels; channel++) {
            audioData = audioBuffer.getChannelData(channel);
            offset = channel;
            decrement = 50;
            for (i = 0; i < length; i++) {
                audioData[i] = this.samples[offset];
                /* fadein */
                // if (i < 50) {
                //     audioData[i] =  (audioData[i] * i) / 50;
                // }
                /* fadeout*/
                // if (i >= (length - 51)) {
                //     audioData[i] =  (audioData[i] * decrement--) / 50;
                // }
                offset += this.option.channels;
            }
            // for (i = 0; i < length; i++) {
            //     audioData[i] = data[offset+i];
            //     offset = channel*length;
            // }
        }
        
        if (this.startTime < this.audioCtx.currentTime) {
            this.startTime = this.audioCtx.currentTime;
        }
    
        bufferSource.buffer = audioBuffer;
        bufferSource.connect(this.gainNode);
        bufferSource.start(this.startTime);
        this.startTime += audioBuffer.duration;
        this.samples = new Float32Array();
    };
    
    getTimestamp() {
        if (this.audioCtx) {
            return this.audioCtx.currentTime;
        } else {
            return 0;
        }
    };

    getBufferingTime() {
        return (this.startTime > this.audioCtx.currentTime) ? (this.startTime - this.audioCtx.currentTime) * 1000 : 0;
    };
    
    play(data) {
        if (!this.isTypedArray(data)) {
            return;
        }
    
        data = this.getFormatedValue(data);
        if (!data.length) {
            return;
        }
    
        var bufferSource = this.audioCtx.createBufferSource(),
            length = data.length / this.option.channels,
            audioBuffer = this.audioCtx.createBuffer(this.option.channels, length, this.option.sampleRate),
            audioData,
            channel,
            offset,
            i,
            decrement;
    
        for (channel = 0; channel < this.option.channels; channel++) {
            audioData = audioBuffer.getChannelData(channel);
            offset = channel;
            decrement = 50;
            for (i = 0; i < length; i++) {
                audioData[i] = data[offset];
                /* fadein */
                // if (i < 50) {
                //     audioData[i] = (audioData[i] * i) / 50;
                // }
                /* fadeout*/
                // if (i >= (length - 51)) {
                //     audioData[i] = (audioData[i] * decrement--) / 50;
                // }
                offset += this.option.channels;
            }
            // for (i = 0; i < length; i++) {
            //         audioData[i] = data[offset+i];
            //         offset = channel*length;
            // }
        }
    
        if (this.startTime < this.audioCtx.currentTime) {
            this.startTime = this.audioCtx.currentTime;
        }
    
        bufferSource.buffer = audioBuffer;
        bufferSource.connect(this.gainNode);
        bufferSource.start(this.startTime);
        this.startTime += audioBuffer.duration;
    };
    
    pause() {
        if (this.audioCtx.state === 'running') {
            this.audioCtx.suspend()
        }
    };
    
    resume() {
        if (this.audioCtx.state === 'suspended') {
            this.audioCtx.resume()
        }
    };
    
}

export default PCMRenderer;
