import uluMediaRefClock from '../common/ulu-media-clock';
import WasmMediaConst from '../common/ulu-media-const.js';
import ErrorDetails from './player-errors.js';
//import Transmuxer from '../core/transmuxer.js';
import Log from '../utils/logger.js';
//import AudioPlayer from '../render/audio-player.js';
import WebGLRenderer from '../render/webgl-renderer.js';
import PCMRenderer from '../render/pcm-renderer.js';

class WasmPlayer {

    constructor(config) {
        this.TAG = 'WasmPlayer';
        this._type = 'WasmPlayer';

        this._config = config;

        this._decodeThreadOpened = false;
        this._decodeThreadWorker = null;
        this._decodeThreadProcessAudio = false;
        this._decodeThreadProcessVideo = false;

        this._transmuxer = null;

        this._videoSamples = [];
        this._audioSamples = [];
        this._playVideoInterval =null;
        this._plauAudioInterval=null;
        this._mediaRefClock=new uluMediaRefClock() ;
        this.audioRenderer      = null;
        this.canvas             = null;
        this.videoRenderer      = null;
        this.callback           = null;
        this.audioTimeOffset    = 0;
        this.playerState        = WasmMediaConst.playerStateIdle;
        this.playerMiddleState = WasmMediaConst.playerMiddleStateNone;
    
        this.videoDecoderType = WasmMediaConst.kDecoderTypeUnknown;
        this.audioDecoderType = WasmMediaConst.kDecoderTypeUnknown;
    
        this.enableAudio = true;
        this.audioObjectType    =0 ;
        this.samplingFrequencyIndex=0,
        this.channelConfiguration=0;
        this.videoSeqData = null;
    
        this._last_audio_timestamp = -1;
        this._last_video_timestamp = -1;
        this._first_video_flag = false;
        this._first_audio_flag = false;
        this._video_timestamp_offset = 0;
        this._audio_timestamp_offset = 0;
        this._decodeThreadWorker = null;
        this.initDecodeThreadWorker();
        this.tryOpenDecodeThread();
        this.needAddPcmBytes = 0;
        this.audioSample = null;
        this.audioRenderer = null;
        // this.initAudioRender();
        this.offset = 0;
        this.pcmTimestamp = 0;
        this.leftBytes = 0;
        this.latency = 100;
        this.waitStartTime = 0;
        this.needWaitTime = 0;
    
        this.audioParam = {
            sampleRate:44100,
            channel:2,
            sampleBits:32
        };
    
        this.sampleSize = this.audioParam.channel * this.audioParam.sampleBits / 8;
        this.stepSize = (this.audioParam.channel * this.audioParam.sampleRate * this.audioParam.sampleBits) / 50;

        this._mediaInfo = null;
        this._statisticsInfo = null;

        this._audioPlayTimerId = null;
        this._videoPlayTimerId = null;
    }

    getSampleBitsBySampleFormat(sampleFormat) {
        if(sampleFormat == WasmMediaConst.kAudioSample8bitInt) {
            return 8;
        } else if(sampleFormat == WasmMediaConst.kAudioSample16bitInt) {
            return 16;
        } else if(sampleFormat == WasmMediaConst.kAudioSample32bitInt) {
            return 32;
        } else if(sampleFormat == WasmMediaConst.kAudioSample32bitFloat) {
            return 32;
        } else if(sampleFormat == WasmMediaConst.kAudioSample64bitDouble) {
            return 64;
        } else if(sampleFormat == WasmMediaConst.kAudioSample8bitIntPlanar) {
            return 8;
        } else if(sampleFormat == WasmMediaConst.kAudioSample16bitIntPlanar) {
            return 16;
        } else if(sampleFormat == WasmMediaConst.kAudioSample32bitIntPlanar) {
            return 32;
        } else if(sampleFormat == WasmMediaConst.kAudioSample32bitFloatPlanar) {
            return 32;
        } else if(sampleFormat == WasmMediaConst.kAudioSample64bitDoublePlanar) {
            return 64;
        }
    
        return 16;
    }

    initAudioRender(sampleRate, channel, sampleBits) {
        if(!this.audioRenderer) {
            let encodingstr = '32bitFloat';
            if(sampleBits == 8) {
                encodingstr = '8bitInt';
            } else if(sampleBits == 16) {
                encodingstr = '16bitInt';
            }
            this.audioRenderer = new PCMRenderer({
                encoding: encodingstr,
                channels: channel,
                sampleRate: sampleRate,
                flushingTime: 1000,
                touchResume:true
            });
        }
    }
    
    initDecodeThreadWorker() {
        var self = this;
        this._decodeThreadWorker = new Worker("../js/decode-thread.js");
        this._decodeThreadWorker.onmessage = function (evt) {
            var objData = evt.data;
            switch (objData.t) {
                case WasmMediaConst.kInitRsp:
                    self.onInitRsp(objData);
                    break;
                case WasmMediaConst.kUninitRsp:
                    self.onUninitRsp();
                    break;
                case WasmMediaConst.kOpenDecoderRsp:
                    self.onOpenDecoderRsp(objData);
                    break;
                case WasmMediaConst.kCloseDecoderRsp:
                    self.onCloseDecoderRsp();
                    break;
                case WasmMediaConst.kSendVideoSampleRsp:
                    self.onSendVideoSampleRsp();
                    break;
                case WasmMediaConst.kSendAudioSampleRsp:
                    self.onSendAudioSampleRsp();
                    break;
                case WasmMediaConst.kProcessVideoRsp:
                    self.onProcessVideoRsp(objData);
                    break;
                case WasmMediaConst.kProcessAudioRsp:
                    self.onProcessAudioRsp(objData);
                    break;
                case WasmMediaConst.kVideoFrameRsp:
                    self.onVideoFrameRsp(objData);
                    break;
                case WasmMediaConst.kAudioFrameRsp:
                    self.onAudioFrameRsp(objData);
                    break;
            }
        }
    };

    tryOpenDecodeThread() {
        this._decodeThreadWorker.postMessage({
            t: WasmMediaConst.kInitReq
        });
    }

    onInitRsp(objData) {
        Log.i(this.TAG, 'Init decoder response ' + objData.e + '.');
        if (objData.e == WasmMediaConst.kOk) {
            this._decodeThreadOpened = true;
            if (this.videoSeqData) {
                var req = {
                    t: WasmMediaConst.kOpenDecoderReq,
                    v: 1,
                    d: this.videoDecoderType,
                    seq: this.videoSeqData
                };
                this._decodeThreadWorker.postMessage(req);
            }

            if (this.audioDecoderType!=WasmMediaConst.kDecoderTypeUnknown){
                var req = {
                    t: WasmMediaConst.kOpenDecoderReq,
                    v: 0,
                    d: this.audioDecoderType,
                    sr: 44100
                };
                this._decodeThreadWorker.postMessage(req);
            }
    
            this._decodeThreadWorker.postMessage({
                t: WasmMediaConst.kProcessAudioReq
            });
            this._decodeThreadProcessAudio = true;
    
            this._decodeThreadWorker.postMessage({
                t: WasmMediaConst.kProcessVideoReq
            });
            this._decodeThreadProcessVideo = true;
    
            this.playerState = WasmMediaConst.playerStatePlaying;
            this.playerMiddleState = WasmMediaConst.playerMiddleStateNone;
        } else if(objData.e == WasmMediaConst.kErrorWasmNotLoaded) {
            Log.i(this.TAG, 'WASM module still not loaded, we need retry!');
    
            setTimeout(this.tryOpenDecodeThread.bind(this), 100);
        } else {
            Log.e(this.TAG, 'failed to open decode thread, ' + objData.e + '.');
            this.reportPlayError(objData.e);
        }
    };

    onUninitRsp() {
        this.playerState = WasmMediaConst.playerStateIdle;
        this.playerMiddleState = WasmMediaConst.playerMiddleStateNone;
    };

    onOpenDecoderRsp(objData) {
        // do nothing
        if (objData.e != WasmMediaConst.kOk) {
            Log.e(this.TAG, 'failed to open decoder, ' + objData.e);
            this.reportPlayError(objData.e);
        }
    }

    onCloseDecoderRsp() {
        // do nothing
    }

    onSendVideoSampleRsp() {
        // do nothing
    };

    onSendAudioSampleRsp() {
        // do nothing
    }

    onProcessVideoRsp(objData) {
        if (this.playerState == WasmMediaConst.playerStateIdle || this.playerMiddleState == WasmMediaConst.playerMiddleStateIdle || this.playerState == WasmMediaConst.playerStatePaused) {
            this._decodeThreadProcessVideo = false;
            return;
        }
    
        this._decodeThreadWorker.postMessage({
            t: WasmMediaConst.kProcessVideoReq
        });
    };

    onProcessAudioRsp(objData) {
        if (this.playerState == WasmMediaConst.playerStateIdle || this.playerMiddleState == WasmMediaConst.playerMiddleStateIdle || this.playerState == WasmMediaConst.playerStatePaused) {
            this._decodeThreadProcessAudio = false;
            return;
        }
    
        this._decodeThreadWorker.postMessage({
            t: WasmMediaConst.kProcessAudioReq
        });
    };

    onVideoFrameRsp(objData) {
        if (this.playerState == WasmMediaConst.playerStateIdle || this.playerMiddleState == WasmMediaConst.playerMiddleStateIdle) {
            return;
        }

        this._videoSamples.push(objData);
        // Log.i(this.TAG, 'ffmpeg onVideoFrameRsp time  :'+objData.ts + ' nums: ' + this._videoSamples.length);
    };

    onAudioFrameRsp(objData) {
        if (this.playerState == WasmMediaConst.playerStateIdle || this.playerMiddleState == WasmMediaConst.playerMiddleStateIdle) {
            return;
        }

        var sampleBits = this.getSampleBitsBySampleFormat(objData.f);
        if (this.audioParam.sampleRate != objData.r || this.audioParam.channel != objData.c || this.audioParam.sampleBits != sampleBits) {
            Log.i(this.TAG, 'audio param changed, old: ' + this.audioParam.sampleRate + ', ' + this.audioParam.channel + ', ' + this.audioParam.sampleBits +
                ', new: ' + objData.r + ', ' + objData.c + ', ' + sampleBits);
            this.audioParam.sampleRate = objData.r;
            this.audioParam.channel = objData.c;
            this.audioParam.sampleBits = sampleBits;
    
            this.sampleSize = this.audioParam.channel * this.audioParam.sampleBits / 8;
            this.stepSize = (this.audioParam.channel * this.audioParam.sampleRate * this.audioParam.sampleBits) / 50;
        }

        this.initAudioRender(this.audioParam.sampleRate, this.audioParam.channel, sampleBits);
    
        // Log.i(this.TAG, 'ffmpeg onAudioFrameRsp time  :'+objData.ts);
        this._audioSamples.push(objData);
    };

    startPlay() {
        if(!this._audioPlayTimerId) {
            this._audioPlayTimerId = window.setInterval(this.playAudio.bind(this), 10);
        }
        
        if(!this._videoPlayTimerId) {
            this._videoPlayTimerId = window.setInterval(this.playVideo.bind(this), 10);
        } 
    }

    playAudio() {
        //return ;
        if (this.playerState != WasmMediaConst.playerStateIdle || this.playerMiddleState == WasmMediaConst.playerMiddleStatePlaying) {
        } else {
            return;
        } 
    
        if(this._mediaRefClock.isVideoRenderStart() > 0) {
            // if(!this._first_audio_flag) {
            //     if (this._audioSamples.length > 0) {
            //         let playTime = this._mediaRefClock.getPlaytingTime();
            //         let firstAudioPts = this._audioSamples[0].ts;
            //         if((firstAudioPts < playTime) || (firstAudioPts > playTime + 3000)) {
            //             this._first_audio_flag = true;
            //         }
            //     } 
            //     return ;
            // }

            if(this.needAddPcmBytes>0){
                let pcmBytesAdded = (this.needAddPcmBytes>this.stepSize)?this.stepSize:this.needAddPcmBytes;
                let pcmBytesAddedbuffer = new Uint8Array(pcmBytesAdded);
                //Log.i(this.TAG, 'cbin audio renderAudioSample 00 playingtime:'+this._mediaRefClock.getPlaytingTime() +'  audio playingtime ts '+this.audioSample.ts + ' timediff :'+(this._mediaRefClock.getPlaytingTime()-this.audioSample.ts));
                this.renderAudioSample(pcmBytesAddedbuffer,pcmBytesAdded,0);
                this.needAddPcmBytes -= pcmBytesAdded;
                let needWaitTime = (this.needAddPcmBytes*1000)/(this.sampleSize *this.audioParam.sampleRate);
    
                //Log.i(this.TAG, 'cbin audio  renderAudioSample needWaitTime :'+needWaitTime);
                let playTimeExpire = this._mediaRefClock.getPlaytingTime()- this.audioSample.ts;
    
                if (playTimeExpire>0 /*&&(playTimeExpire<needWaitTime)*/){
                    this.needAddPcmBytes=0;
                }
            }else{
                if(this.leftBytes>0){
                    if(this.leftBytes>this.stepSize) {
                        this.leftBytes -= this.stepSize;
                        let audiobuffer = this.audioSample.slice(this.offset.this.offset + this.stepSize);
                        this.pcmTimestamp += this.stepSize * 1000 / ((this.sampleSize * this.audioParam.sampleRate));
                        this.offset += this.stepSize;
                        this.renderAudioSample(audiobuffer, this.stepSize, this.pcmTimestamp);
                    }else{
                        let audiobuffer = this.audioSample.slice(this.offset.this.offset + this.leftBytes);
                        this.pcmTimestamp += this.leftBytes * 1000 / ((this.sampleSize * this.audioParam.sampleRate));
                        this.renderAudioSample(audiobuffer, this.leftBytes, this.pcmTimestamp);
                        this.offset = 0;
                        this.pcmTimestamp=0;
                        this.leftBytes=0;
                    }
                } else {
                    if(this.needWaitTime>0){
                        if(this.needWaitTime<= this._mediaRefClock.getPlaytingTime()-this.waitStartTime){
                              this.waitStartTime=0;
                              this.needWaitTime=0;
                        }
                        return ;
                    }else {
                        if (this._audioSamples.length > 0) {
                            let firstAudioPts = this._audioSamples[0].ts;
                            let playTime = this._mediaRefClock.getPlaytingTime();
                            if(firstAudioPts > playTime+200) {
                                return ;
                            }
                            this.audioSample = this._audioSamples.shift();
                            if (this.audioSample.size > this.stepSize) {
                                this.leftBytes = this.audioSample.size;
                                this.pcmTimestamp = this.audioSample.ts;
                            } else {
                                this.renderAudioSample(this.audioSample.data, this.audioSample.data.length, this.audioSample.ts)
                            }
                        }
                    }
                }
            }
        }
    }

    renderAudioSample(data, size, timestamp) {
        if (timestamp != 0) {
            let playingtime = this._mediaRefClock.getPlaytingTime();
    
            let timestampInAudioRender = timestamp;
            // if (this.audioRenderer) {
            //     let audioRenderBufferingTime = this.audioRenderer.getBufferingTime();
            //     if (audioRenderBufferingTime > 0) {
            //         timestampInAudioRender -= audioRenderBufferingTime;
            //     }
            // }
    
            // Log.i(this.TAG, 'wwplay renderAudioSample playing time: ' + playingtime + ', audio timestamp: ' + timestamp +
            //     ', audio render timestamp: ' + timestampInAudioRender + ', timediff: ' + (playingtime - timestampInAudioRender));
    
            let desiredAudioRenderTime = playingtime + this.latency + 100;
            if (timestampInAudioRender + 200 < playingtime) {
                if(this._audioSamples.length > 0) {
                    if (this._audioSamples[this._audioSamples.length - 1].ts > desiredAudioRenderTime) {
                        let needDropPcmSize = (desiredAudioRenderTime - timestampInAudioRender) * (this.sampleSize * this.audioParam.sampleRate) / 1000;
                        if (needDropPcmSize >= this.sampleSize) {
                            needDropPcmSize -= (needDropPcmSize % this.sampleSize);
                        }
    
                        Log.i(this.TAG, 'wwplay renderAudioSample is too slow, playing time: ' + playingtime + ', audio timestamp: ' + timestamp +
                            ', audio render timestamp: ' + timestampInAudioRender + ', time diff: ' + (playingtime - timestampInAudioRender) + ', latency: ' + this.latency +
                            ', drop pcm size: ' + needDropPcmSize);
    
                        if (needDropPcmSize >= size) {
                            let leftsize = needDropPcmSize - size;
                            let audioSample = null;
                            while (leftsize>0) {
                                if(this._audioSamples.length>0) {
                                    audioSample = this._audioSamples.shift();
                                    leftsize = leftsize - audioSample.data.length;
                                } else {
                                    break;
                                }
                            }
                            if(leftsize<0){
                                if (this.audioRenderer) {
                                    this.audioRenderer.play(new Uint8Array(audioSample.data.slice(audioSample.data.length+leftsize,audioSample.data.length)));
                                }
                                //  needDropPcmSize = 0;
                            }else {
                               // needDropPcmSize = leftsize;
                            }
                            return 0;
                        }else {
                            size -= needDropPcmSize;
                        }
                    }
                }
            } else if(timestampInAudioRender > desiredAudioRenderTime) {
                this.needAddPcmBytes = (timestampInAudioRender - desiredAudioRenderTime) * (this.sampleSize * this.audioParam.sampleRate) / 1000;
                if (this.needAddPcmBytes > this.sampleSize) {
                    this.needAddPcmBytes -= (this.needAddPcmBytes % this.sampleSize);
                }
    
                Log.i(this.TAG, 'wwplay renderAudioSample is too fast, playing time: ' + playingtime + ', audio timestamp: ' + timestamp +
                    ', audio render timestamp: ' + timestampInAudioRender + ', time diff: ' + (playingtime - timestampInAudioRender) + ', latency: ' + this.latency +
                    ', audio size: ' + this._audioSamples.length);
            } else {
                //Log.i(this.TAG, 'renderAudioSample is normal.');
            }
        }
    
        if (this.audioRenderer) {
            this.audioRenderer.play(new Uint8Array(data.slice(0, size)));
        }
    }

    playVideo() {
        if (this.playerState != WasmMediaConst.playerStateIdle || this.playerMiddleState == WasmMediaConst.playerMiddleStatePlaying) {
        } else {
            return;
        } 

        if (this._videoSamples.length > 0){
            let lengths = this._videoSamples.length;
            // let lastVideoTimeInBuffer = this._videoSamples[lengths-1].ts;
            let curVideoTime = this._videoSamples[0].ts;
            let playingTime = this._mediaRefClock.getPlaytingTime();
            // let playspeed = this._mediaRefClock.getSpeed();
            let timediff = curVideoTime-playingTime;
            let needRender = false;
            let needUpdateTime = false;
            let time_modify = curVideoTime;

            if(playingTime < 1) {
                timediff = 0;
                needRender = true;
                this._mediaRefClock.updateByVideoRender(curVideoTime);
            } 
            else if(this._config.isLive) {
                if(lengths < 3 || (this._last_video_timestamp-curVideoTime<200 && this._last_video_timestamp-curVideoTime>0)) {
                    timediff = timediff + 10;
                } 
                else if(lengths > 20 || (lengths > 2 && this._last_video_timestamp-curVideoTime>500)) {
                    timediff = timediff - 20;
                } 
                else if(lengths > 10 || (lengths > 2 && this._last_video_timestamp-curVideoTime>320)) {
                    timediff = timediff - 10;
                } 
            }

            if(timediff<1) {
                needRender = true;
                if(timediff < -3000 || (lengths > 2 && this._last_video_timestamp-curVideoTime>320)) {
                    needUpdateTime = true;
                }
                if(this._config.isLive) {
                    if(lengths > 50 || (lengths>3 && this._last_video_timestamp-curVideoTime>2000)) {
                        time_modify = this._videoSamples[lengths-1].ts;
                        for(let i=0; i<lengths-1; i++) {
                            this._videoSamples.shift();
                        }
                    }
                    else if(lengths > 25 || (lengths>3 && this._last_video_timestamp-curVideoTime>1000) ) { 
                        time_modify = this._videoSamples[1].ts;
                        this._videoSamples.shift();
                    }
                }

                if(needUpdateTime) {
                    // Log.w(this.TAG, 'wwplay need update play time :'+playingTime +' ts :'+curVideoTime + ' timediff :'+timediff +'lengths :' + lengths + 'time_modify :' + time_modify);
                    this._mediaRefClock.forceUpdateByVideo(time_modify);
                }
            } else if(timediff>3000) {
                needRender = true;
                Log.w(this.TAG, 'wwplay video play too fast, time :'+playingTime +' ts :'+curVideoTime + ' timediff :'+timediff +'lengths :' + lengths + 'time_modify :' + time_modify);
                this._mediaRefClock.forceUpdateByVideo(time_modify);
            }

            if(needRender) {
                // Log.i(this.TAG, 'wwplay videotime:'+playingTime +' ts :'+curVideoTime + ' timediff :'+timediff +'lengths :' + lengths + 'time_modify :' + time_modify);
                
                let sample = this._videoSamples.shift();
                this.renderVideoFrame(sample.data, sample.w, sample.h, sample.y, sample.uv, time_modify);
            }
        }
    }

    renderVideoFrame(data, width, height, yStride, uvStride, pts) {
        if (this.videoRenderer) {
            //Log.i(this.TAG, 'vrender, timestamp: ' + pts + ', playing time: ' + this._mediaRefClock.getPlaytingTime() +
               // ', last time: ' + this._last_video_timestamp + ', offset: ' + (this._last_video_timestamp - pts));
            this.videoRenderer.renderFrame(data, width, height, yStride * height, uvStride * height / 2);
        }
    
        //this._mediaRefClock.updateByVideoRender(pts);
    };

    play(videoorcanvas) {
        if (this.playerMiddleState != WasmMediaConst.playerMiddleStateNone) {
            Log.i(this.TAG, 'middle state, need try');
            return {e: WasmMediaConst.kErrorNeedRetry, m: "middle state"};
        }
    
        var ret = {
            e: WasmMediaConst.kOk,
            m: "Success"
        };
    
        do {
            if (this.playerState == WasmMediaConst.playerStatePaused) {
                ret = this.resume();
                break;
            }

            if (!videoorcanvas) {
                ret = {
                    e: WasmMediaConst.kErrorInvalidCanvas,
                    m: "Canvas not set"
                };
                Log.e(this.TAG, 'failed to play, canvas empty.');
                break;
            }

            if(!this.canvas) {
                this.canvas = videoorcanvas;
            }

            if(!this.videoRenderer) {
                this.videoRenderer = new WebGLRenderer(this.canvas, {});
            }
    
            if (this.playerState == WasmMediaConst.playerStatePlaying) {
                break;
            }
    
            this.playerMiddleState = WasmMediaConst.playerMiddleStatePlaying;
        } while (false);
    
        this.startPlay();
    };

    pause() {
        if (this.playerMiddleState != WasmMediaConst.playerMiddleStateNone) {
            Log.i(this.TAG, 'middle state, need try');
            return {e: WasmMediaConst.kErrorNeedRetry, m: "middle state"};
        }
    
        Log.i(this.TAG, 'Pause.');
    
        var ret = {
            e: WasmMediaConst.kOk,
            m: "Success"
        };
    
        if (this.playerState != WasmMediaConst.playerStatePlaying) {
            ret = {
                e: WasmMediaConst.kErrorInvalidState,
                m: "Not playing"
            };
            return ret;
        }
    
        if (this.audioRenderer) {
            this.audioRenderer.pause();
        }
    
        this.playerState = WasmMediaConst.playerStatePaused;
    
        return ret;
    };

    resume() {
        Log.i(this.TAG, 'Resume.');
    
        var ret = {
            e: WasmMediaConst.kOk,
            m: "Success"
        };
    
        if (this.playerState != WasmMediaConst.playerStatePaused) {
            ret = {
                e: WasmMediaConst.kErrorInvalidState,
                m: "Not pausing"
            };
            return ret;
        }
    
        if (this.audioRenderer) {
            this.audioRenderer.resume();
        }
    
        this.playerState = WasmMediaConst.playerStatePlaying;
    
    
        if (this._decodeThreadProcessVideo == false) {
            this._decodeThreadWorker.postMessage({
                t: kProcessVideoReq
            });
        }
    
        if(this._decodeThreadProcessAudio ==false){
            this._decodeThreadWorker.postMessage({
                t:kProcessAudioReq
            });
        }
    
        return ret;
    };

    stop() {
        if (this.playerMiddleState != WasmMediaConst.playerMiddleStateNone) {
            Log.i(this.TAG, 'middle state, need try');
            return {e: WasmMediaConst.kErrorNeedRetry, m: "middle state"};
        }
    
        Log.i(this.TAG, 'Stop.');
    
        var ret = {
            e: WasmMediaConst.kOk,
            m: "Success"
        };
    
        if (this.playerState == WasmMediaConst.playerStateIdle) {
            ret = {
                e: WasmMediaConst.kErrorInvalidState,
                m: "Not playing"
            };
            return ret;
        }
    
        this.playerMiddleState = WasmMediaConst.playerMiddleStateIdle;

        if (this._audioPlayTimerId) {
            window.clearInterval(this._audioPlayTimerId);
            this._audioPlayTimerId = null;
        }

        if (this._videoPlayTimerId) {
            window.clearInterval(this._videoPlayTimerId);
            this._videoPlayTimerId = null;
        }
    
        return ret;
    };

    fullscreen() {
        if (this.videoRenderer) {
            this.videoRenderer.fullscreen();
        }
    };

    enableaudio(enable) {
        this.enableAudio = enable;
    }

    setVideoRenderMode(renderMode) {
        if (this.videoRenderer) {
            this.videoRenderer.setRenderMode(renderMode);
        }
    };

    getState() {
        return this.playerState;
    };

    reportPlayError(error, message) {
        var e = {
            e: error || 0,
            m: message
        };
    
        if (this.callback) {
            this.callback(e);
        }
    };

    destroy() {
        this.stop();

        this.canvas             = null;
        this.videoRenderer      = null;
        this.callback           = null;
    
        this.audioTimeOffset    = 0;

        if(this.audioRenderer){
            this.audioRenderer.stop;
        }
    
        this.videoDecoderType = WasmMediaConst.kDecoderTypeUnknown;
        this.audioDecoderType = WasmMediaConst.kDecoderTypeUnknown;
        if(!this._decodeThreadWorker) {
            return;
        }

        Log.i(this.TAG, 'closing audio decoder.');
        this._decodeThreadWorker.postMessage({
            t: WasmMediaConst.kCloseDecoderReq,
            v: 0
        });
    
        Log.i(this.TAG, 'closing video decoder.');
        this._decodeThreadWorker.postMessage({
            t: WasmMediaConst.kCloseDecoderReq,
            v: 1
        });
    
        Log.i(this.TAG, 'uninit ffmpeg decoder.');
        this._decodeThreadWorker.postMessage({
            t: WasmMediaConst.kUninitReq
        });
        this._decodeThreadWorker.terminate();
        this._decodeThreadWorker = null;
        this.videoSeqData = null;
    };

    load(transmuxer, cb) {
        this._transmuxer = transmuxer;
        this.callback = cb;
        //this.initDecodeThreadWorker();
        //this.tryOpenDecodeThread();
    };

    OnInitSegment(type, is) {
        // Log.i(this.TAG, 'OnInitSegment, type: ' + type + ', codec: ' + is.codec);
        if (type === 'audio' && this.audioDecoderType == WasmMediaConst.kDecoderTypeUnknown) {
            if (is.codec.search("mp4a") != -1) {
                // this.audioObjectType = 2;
                // this.samplingFrequencyIndex = this.getSamplingFrequencyIndex(is.audioSampleRate);
                // this.channelConfiguration = is.metadata.channelCount;
                // Log.i(this.TAG, 'it is aac decoder, audio type: ' + this.audioObjectType + ', sample rate: ' + is.metadata.audioSampleRate + ', index: ' +
                //     this.samplingFrequencyIndex + ', channel configuration: ' + this.channelConfiguration);
                if(this.audioDecoderType == WasmMediaConst.kDecoderTypeUnknown) {
                    Log.i(this.TAG, 'it is aac decoder.');
                } 
                this.audioDecoderType = WasmMediaConst.kAudioDecoderAac;
                if (this._decodeThreadOpened) {
                    var req = {
                        t: WasmMediaConst.kOpenDecoderReq,
                        v: 0,
                        d: this.audioDecoderType,
                        sr: 44100
                    };
                    this._decodeThreadWorker.postMessage(req);
                }
            }
            // else if(is.codec.search("mp3") != -1) {
            //     Log.i(this.TAG, 'it is mp3 decoder.');
            //     this.audioDecoderType = WasmMediaConst.kAudioDecoderMp3;
            //     if (this._decodeThreadOpened) {
            //         var req = {
            //             t: WasmMediaConst.kOpenDecoderReq,
            //             v: 0,
            //             d: this.audioDecoderType,
            //             sr: 44100
            //         };
            //         this._decodeThreadWorker.postMessage(req);
            //     }
            // }
            else {
                Log.e(this.TAG, 'it is unknown decoder, ' + metadata.codec);
                return;
            }
        } else if (type === 'video' && this.videoDecoderType == WasmMediaConst.kDecoderTypeUnknown) {
            if (is.codec.search("avc1") != -1) {
                if(this.videoDecoderType == WasmMediaConst.kDecoderTypeUnknown) {
                    Log.i(this.TAG, 'it is h264 decoder.');
                }
                this.videoDecoderType = WasmMediaConst.kVideoDecoderH264;
                this.videoSeqData = is.metadata;
                if (this._decodeThreadOpened) {
                    var req = {
                        t: WasmMediaConst.kOpenDecoderReq,
                        v: 1,
                        d: this.videoDecoderType,
                        seq: is.metadata
                    };
                    this._decodeThreadWorker.postMessage(req);
                }
            }
            else if(is.codec.search("h265") != -1) {
                if(this.videoDecoderType == WasmMediaConst.kDecoderTypeUnknown) {
                    Log.i(this.TAG, 'it is h265 decoder.');
                }
                this.videoDecoderType = WasmMediaConst.kVideoDecoderH265;
                this.videoSeqData = is.metadata;
                if (this._decodeThreadOpened) {
                    var req = {
                        t: WasmMediaConst.kOpenDecoderReq,
                        v: 1,
                        d: this.videoDecoderType,
                        seq: is.metadata
                    };
                    this._decodeThreadWorker.postMessage(req);
                }
            }
            else {
                Log.e(this.TAG, 'it is unknown decoder, ' + metadata.codec);
                return;
            }
        } else {
            // do nothing
        }
    };
    OnMediaSegment(type, ms) {
        let samples = ms.samples;
        if (!samples || samples.length === 0) {
            return;
        }

        var t = null;
        if(type == 'video') {
            t = WasmMediaConst.kSendVideoSampleReq;
            this._last_video_timestamp = samples[samples.length-1].pts;
        } else {
            if(this.enableAudio == false) {
                ms.samples = [];
                ms.sampleCount = 0;
                return ;
            }
            t = WasmMediaConst.kSendAudioSampleReq;
            this._last_audio_timestamp = samples[samples.length-1].pts;
        }

        for (let i = 0; i < samples.length; i++) {
            let sample = samples[i];
            var req = {
                t: t,
                pts: sample.pts,
                isKeyFrame: sample.isKeyframe,
                data: sample.data,
                size: sample.size
            };
            this._decodeThreadWorker.postMessage(req);
            if(this._videoSamples.length > 0 && sample.isKeyframe){
                Log.i(this.TAG, '_last_video_timestamp: ' + this._last_video_timestamp + ' render: ' + this._videoSamples[0].ts + 
                ' diff: ' + (this._last_video_timestamp-this._videoSamples[0].ts) +  ' length: ' + this._videoSamples.length);
            }
        }
        // clean the cache data of track
        ms.samples = [];
        ms.sampleCount = 0;
    };
    OnLoadComplete() {
        Log.i(this.TAG, 'OnLoadComplete');
        this.reportPlayError(WasmMediaConst.kErrorLoadComplete, ErrorDetails.MEDIA_MSE_ERROR);
    };
    OnRecoverdEarlyEof() {
        Log.i(this.TAG, 'OnRecoverdEarlyEof');
        this.stop();
        this.reportPlayError(WasmMediaConst.kErrorNetwork, ErrorDetails.NETWORK_UNRECOVERABLE_EARLY_EOF);
    };
    OnIoErr(detail, info) {
        Log.i(this.TAG, 'OnIoErr');
        this.stop();
        this.reportPlayError(WasmMediaConst.kErrorNetwork, ErrorDetails.NETWORK_EXCEPTION);
    };
    OnDemuxErr(detail, info) {
        Log.i(this.TAG, 'OnDemuxErr');
        this.stop();
        this.reportPlayError(WasmMediaConst.kErrorMedia, ErrorDetails.MEDIA_FORMAT_ERROR);
    };
    OnMediaInfo(mediaInfo) {
        this._mediaInfo = mediaInfo;
    };
    OnStatisticsInfo(statInfo) {
        this._statisticsInfo = statInfo;
    };
    OnRecommendSeekPoint(milliseconds) {
    };

    get type() {
        return this._type;
    };
    get buffered() {
        return null;
    };
    get duration() {
        return -1;
    };
    get volume() {
        if(this.audioRenderer){
           return this.audioRenderer.volume();
        }
        return 0;
    };
    set volume(value) {
        if(this.audioRenderer){
            this.audioRenderer.volume(value);
        }
    };
    get muted() {
        if(this.audioRenderer){
            return this.audioRenderer.muted();
        }
        return false;
    };
    set muted(muted) {
        if(this.audioRenderer){
            return this.audioRenderer.muted(muted);
        }
    };
    get currentTime() {
        if(this._mediaRefClock) {
            return this._mediaRefClock.getPlaytingTime();
        }
    };
    set currentTime(seconds) {
        Log.w(this.TAG, 'dont support seek time');
    };
    get mediaInfo() {
        if(this._mediaInfo) {
            return Object.assign({}, this._mediaInfo);
        }
    };
    get statisticsInfo() {
        if(this._statisticsInfo) {
            return Object.assign({}, this._statisticsInfo);
        }
    };
}

export default WasmPlayer;