// import EventEmitter from 'events';
import Log from '../utils/logger.js';
import Browser from '../utils/browser.js';
// import PlayerEvents from './player-events.js';
// import Transmuxer from '../core/transmuxer.js';
import MSEController from '../core/mse-controller.js';
import MSEEvents from '../core/mse-events.js';
import WasmMediaConst from '../common/ulu-media-const.js';
import ErrorDetails from './player-errors.js';

class MsePlayer {

    constructor(config) {
        this.TAG = 'MsePlayer';
        this._type = 'MsePlayer';
        // this._emitter = new EventEmitter();

        this._config = config;

        this.e = {
            onvLoadedMetadata: this._onvLoadedMetadata.bind(this),
            onvSeeking: this._onvSeeking.bind(this),
            onvCanPlay: this._onvCanPlay.bind(this),
            onvStalled: this._onvStalled.bind(this),
            onvProgress: this._onvProgress.bind(this)
        };

        if (self.performance && self.performance.now) {
            this._now = self.performance.now.bind(self.performance);
        } else {
            this._now = Date.now;
        }

        this._pendingSeekTime = null;  // in seconds
        this._requestSetTime = false;
        this._seekpointRecord = null;
        this._progressChecker = null;

        this._mediaElement = null;
        this._msectl = null;
        this._transmuxer = null;

        this._mseSourceOpened = false;
        this._hasPendingLoad = false;
        this._receivedCanPlay = false;

        this.callback = null;
        this._mediaInfo = null;
        this._statisticsInfo = null;

        let chromeNeedIDRFix = (Browser.chrome &&
                               (Browser.version.major < 50 ||
                               (Browser.version.major === 50 && Browser.version.build < 2661)));
        this._alwaysSeekKeyframe = (chromeNeedIDRFix || Browser.msedge || Browser.msie) ? true : false;

        if (this._alwaysSeekKeyframe) {
            this._config.accurateSeek = false;
        }

        this._checkAndDoSeekTimerId = null;
        // if (this._config.isLive && this._config.enableCheckAndDoSeek) {
        //     Log.v(this.TAG, 'it is LIVE stream and enable checkAndDoSeek!');
        //     this._checkAndDoSeekTimerId = window.setInterval(this._checkAndDoSeek.bind(this), 5000);
        // }

        this._checkBufferingTimerId = null;
        // if (this._config.isLive && this._config.enableCheckBuffering) {
        //     Log.v(this.TAG, 'it is LIVE stream and enable checkBuffering!');
        //     this._checkBufferingTimerId = window.setInterval(this._checkBuffering.bind(this), 1000);
        // }

        this._pausedSinceBuffering = false;
    }

    destroy() {
        if (this._config.isLive && this._config.enableCheckAndDoSeek) {
            window.clearInterval(this._checkAndDoSeekTimerId);
        }

        if (this._config.isLive && this._config.enableCheckBuffering) {
            window.clearInterval(this._checkBufferingTimerId);
        }

        if (this._progressChecker != null) {
            window.clearInterval(this._progressChecker);
            this._progressChecker = null;
        }
       
        this.unload();
        
        if (this._mediaElement) {
            this._detachMediaElement();
        }
        this.e = null;

        // this._emitter.removeAllListeners();
        // this._emitter = null;
    }

    // on(event, listener) {
    //     if (event === PlayerEvents.MEDIA_INFO) {
    //         if (this._mediaInfo != null) {
    //             Promise.resolve().then(() => {
    //                 this._emitter.emit(PlayerEvents.MEDIA_INFO, this.mediaInfo);
    //             });
    //         }
    //     } else if (event === PlayerEvents.STATISTICS_INFO) {
    //         if (this._statisticsInfo != null) {
    //             Promise.resolve().then(() => {
    //                 this._emitter.emit(PlayerEvents.STATISTICS_INFO, this.statisticsInfo);
    //             });
    //         }
    //     }
    //     this._emitter.addListener(event, listener);
    // }

    // off(event, listener) {
    //     this._emitter.removeListener(event, listener);
    // }

    _attachMediaElement(mediaElement) {
        this._mediaElement = mediaElement;
        mediaElement.addEventListener('loadedmetadata', this.e.onvLoadedMetadata);
        mediaElement.addEventListener('seeking', this.e.onvSeeking);
        mediaElement.addEventListener('canplay', this.e.onvCanPlay);
        mediaElement.addEventListener('stalled', this.e.onvStalled);
        mediaElement.addEventListener('progress', this.e.onvProgress);

        this._msectl = new MSEController(this._config);

        this._msectl.on(MSEEvents.UPDATE_END, this._onmseUpdateEnd.bind(this));
        this._msectl.on(MSEEvents.BUFFER_FULL, this._onmseBufferFull.bind(this));
        this._msectl.on(MSEEvents.SOURCE_OPEN, () => {
            this._mseSourceOpened = true;
            if (this._hasPendingLoad) {
                this._hasPendingLoad = false;
                //this.load();
            }
        });
        this._msectl.on(MSEEvents.ERROR, (info) => {
            this.reportMessage(WasmMediaConst.kErrorMedia, ErrorDetails.MEDIA_MSE_ERROR);
            // this._emitter.emit(PlayerEvents.ERROR,
            //     ErrorTypes.MEDIA_ERROR,
            //     ErrorDetails.MEDIA_MSE_ERROR,
            //     info);

        });

        this._msectl.attachMediaElement(mediaElement);

        if (this._pendingSeekTime != null) {
            try {
                mediaElement.currentTime = this._pendingSeekTime;
                this._pendingSeekTime = null;
            } catch (e) {
                // IE11 may throw InvalidStateError if readyState === 0
                // We can defer set currentTime operation after loadedmetadata
            }
        }
    }

    _detachMediaElement() {
        if (this._mediaElement) {
            this._msectl.detachMediaElement();
            this._mediaElement.removeEventListener('loadedmetadata', this.e.onvLoadedMetadata);
            this._mediaElement.removeEventListener('seeking', this.e.onvSeeking);
            this._mediaElement.removeEventListener('canplay', this.e.onvCanPlay);
            this._mediaElement.removeEventListener('stalled', this.e.onvStalled);
            this._mediaElement.removeEventListener('progress', this.e.onvProgress);
            this._mediaElement = null;
        }
        if (this._msectl) {
            this._msectl.destroy();
            this._msectl = null;
        }
    }

    OnInitSegment(type, is) {
        this._msectl.appendInitSegment(is);
    }
    OnMediaSegment(type, ms) {
        this._msectl.appendMediaSegment(ms);

        // lazyLoad check
        if (this._config.lazyLoad && !this._config.isLive) {
            let currentTime = this._mediaElement.currentTime;
            if (ms.info.endDts >= (currentTime + this._config.lazyLoadMaxDuration) * 1000) {
                if (this._progressChecker == null) {
                    Log.v(this.TAG, 'Maximum buffering duration exceeded, suspend transmuxing task');
                    this._suspendTransmuxer();
                }
            }
        }
    }
    OnLoadComplete() {
        Log.i(this.TAG, 'OnLoadComplete');
        this._msectl.endOfStream();
        this.reportMessage(WasmMediaConst.kErrorLoadComplete, ErrorDetails.MEDIA_MSE_ERROR);
        // this._emitter.emit(PlayerEvents.LOADING_COMPLETE);
    }
    OnRecoverdEarlyEof() {
        Log.i(this.TAG, 'OnRecoverdEarlyEof');
        // this._emitter.emit(PlayerEvents.RECOVERED_EARLY_EOF);
        this.reportMessage(WasmMediaConst.kErrorNetwork, ErrorDetails.NETWORK_UNRECOVERABLE_EARLY_EOF);
    }
    OnIoErr(detail, info) {
        Log.i(this.TAG, 'OnIoErr');
        // this._emitter.emit(PlayerEvents.ERROR, ErrorTypes.NETWORK_ERROR, detail, info);
        this.reportMessage(WasmMediaConst.kErrorNetwork, ErrorDetails.NETWORK_EXCEPTION);
    }
    OnDemuxErr(detail, info) {
        Log.i(this.TAG, 'OnDemuxErr');
        // this._emitter.emit(PlayerEvents.ERROR, ErrorTypes.MEDIA_ERROR, detail, {code: -1, msg: info});
        this.reportMessage(WasmMediaConst.kErrorMedia, ErrorDetails.MEDIA_FORMAT_ERROR);
    }
    OnMediaInfo(mediaInfo) {
        this._mediaInfo = mediaInfo;
        // this._emitter.emit(PlayerEvents.MEDIA_INFO, Object.assign({}, mediaInfo));
    }
    OnStatisticsInfo(statInfo) {
        this._statisticsInfo = this._fillStatisticsInfo(statInfo);
        // this._emitter.emit(PlayerEvents.STATISTICS_INFO, Object.assign({}, this._statisticsInfo));
    }
    OnRecommendSeekPoint(milliseconds) {
        if (this._mediaElement && !this._config.accurateSeek) {
            this._requestSetTime = true;
            this._mediaElement.currentTime = milliseconds / 1000;
        }
    }

    load(transmuxer, cb) {

        if (this._config.isLive && this._config.enableCheckAndDoSeek) {
            Log.v(this.TAG, 'it is LIVE stream and enable checkAndDoSeek!');
            this._checkAndDoSeekTimerId = window.setInterval(this._checkAndDoSeek.bind(this), 5000);
        }

        if (this._config.isLive && this._config.enableCheckBuffering) {
            Log.v(this.TAG, 'it is LIVE stream and enable checkBuffering!');
            this._checkBufferingTimerId = window.setInterval(this._checkBuffering.bind(this), 1000);
        }

        this._transmuxer = transmuxer;
        this.callback = cb;

        if (this._hasPendingLoad) {
            return;
        }

        if (this._config.deferLoadAfterSourceOpen && this._mseSourceOpened === false) {
            this._hasPendingLoad = true;
            return;
        }

        // if (this._mediaElement.readyState > 0) {
        //     this._requestSetTime = true;
        //     // IE11 may throw InvalidStateError if readyState === 0
        //     this._mediaElement.currentTime = 0;
        // }
    }

    unload() {
        if (this._mediaElement) {
            this._mediaElement.pause();
        }
        if (this._msectl) {
            this._msectl.seek(0);
        }
        // if (this._transmuxer) {
        //     this._transmuxer.close();
        //     this._transmuxer.destroy();
        //     this._transmuxer = null;
        // }
    }

    reportMessage(error, status, message) {
        var e = {
            error: error || 0,
            status: status || 0,
            message: message
        };
    
        if (this.callback) {
            this.callback(e);
        }
    }


    play(videoorcanvas) {
        this._attachMediaElement(videoorcanvas);

        if(this._mediaElement) {
            return this._mediaElement.play();
        }
    }

    pause() {
        if(this._mediaElement) {
            return this._mediaElement.pause();
        }
    }

    resume() {
        if(this._mediaElement) {
            return this._mediaElement.play();
        }
    }

    stop() {
        if(this._mediaElement) {
            return this._mediaElement.pause();
        }
    }

    get type() {
        return this._type;
    }

    get buffered() {
        return this._mediaElement.buffered;
    }

    get duration() {
        return this._mediaElement.duration;
    }

    get volume() {
        return this._mediaElement.volume;
    }

    set volume(value) {
        this._mediaElement.volume = value;
    }

    get muted() {
        return this._mediaElement.muted;
    }

    set muted(muted) {
        this._mediaElement.muted = muted;
    }

    get currentTime() {
        if (this._mediaElement) {
            return this._mediaElement.currentTime;
        }
        return 0;
    }

    set currentTime(seconds) {
        if (this._mediaElement) {
            this._internalSeek(seconds);
        } else {
            this._pendingSeekTime = seconds;
        }
    }

    get mediaInfo() {
        return Object.assign({}, this._mediaInfo);
    }

    get statisticsInfo() {
        if (this._statisticsInfo == null) {
            this._statisticsInfo = {};
        }
        this._statisticsInfo = this._fillStatisticsInfo(this._statisticsInfo);
        return Object.assign({}, this._statisticsInfo);
    }

    _fillStatisticsInfo(statInfo) {
        statInfo.playerType = this._type;

        if (!(this._mediaElement instanceof HTMLVideoElement)) {
            return statInfo;
        }

        let hasQualityInfo = true;
        let decoded = 0;
        let dropped = 0;

        if (this._mediaElement.getVideoPlaybackQuality) {
            let quality = this._mediaElement.getVideoPlaybackQuality();
            decoded = quality.totalVideoFrames;
            dropped = quality.droppedVideoFrames;
        } else if (this._mediaElement.webkitDecodedFrameCount != undefined) {
            decoded = this._mediaElement.webkitDecodedFrameCount;
            dropped = this._mediaElement.webkitDroppedFrameCount;
        } else {
            hasQualityInfo = false;
        }

        if (hasQualityInfo) {
            statInfo.decodedFrames = decoded;
            statInfo.droppedFrames = dropped;
        }

        return statInfo;
    }

    _onmseUpdateEnd() {
        if (!this._config.lazyLoad || this._config.isLive) {
            return;
        }

        let buffered = this._mediaElement.buffered;
        let currentTime = this._mediaElement.currentTime;
        let currentRangeStart = 0;
        let currentRangeEnd = 0;

        for (let i = 0; i < buffered.length; i++) {
            let start = buffered.start(i);
            let end = buffered.end(i);
            if (start <= currentTime && currentTime < end) {
                currentRangeStart = start;
                currentRangeEnd = end;
                break;
            }
        }

        if (currentRangeEnd >= currentTime + this._config.lazyLoadMaxDuration && this._progressChecker == null) {
            Log.v(this.TAG, 'Maximum buffering duration exceeded, suspend transmuxing task');
            this._suspendTransmuxer();
        }
    }

    _onmseBufferFull() {
        Log.v(this.TAG, 'MSE SourceBuffer is full, suspend transmuxing task');
        if (this._progressChecker == null) {
            this._suspendTransmuxer();
        }
    }

    _suspendTransmuxer() {
        if (this._transmuxer) {
            this._transmuxer.pause();

            if (this._progressChecker == null) {
                this._progressChecker = window.setInterval(this._checkProgressAndResume.bind(this), 1000);
            }
        }
    }

    _checkProgressAndResume() {
        let currentTime = this._mediaElement.currentTime;
        let buffered = this._mediaElement.buffered;

        let needResume = false;

        for (let i = 0; i < buffered.length; i++) {
            let from = buffered.start(i);
            let to = buffered.end(i);
            if (currentTime >= from && currentTime < to) {
                if (currentTime >= to - this._config.lazyLoadRecoverDuration) {
                    needResume = true;
                }
                break;
            }
        }

        if (needResume) {
            window.clearInterval(this._progressChecker);
            this._progressChecker = null;
            if (needResume) {
                Log.v(this.TAG, 'Continue loading from paused position');
                this._transmuxer.resume();
            }
        }
    }

    _isTimepointBuffered(seconds) {
        let buffered = this._mediaElement.buffered;

        for (let i = 0; i < buffered.length; i++) {
            let from = buffered.start(i);
            let to = buffered.end(i);
            if (seconds >= from && seconds < to) {
                return true;
            }
        }
        return false;
    }

    _internalSeek(seconds) {
        let directSeek = this._isTimepointBuffered(seconds);

        let directSeekBegin = false;
        let directSeekBeginTime = 0;

        if (seconds < 1.0 && this._mediaElement.buffered.length > 0) {
            let videoBeginTime = this._mediaElement.buffered.start(0);
            if ((videoBeginTime < 1.0 && seconds < videoBeginTime) || Browser.safari) {
                directSeekBegin = true;
                // also workaround for Safari: Seek to 0 may cause video stuck, use 0.1 to avoid
                directSeekBeginTime = Browser.safari ? 0.1 : videoBeginTime;
            }
        }

        if (directSeekBegin) {  // seek to video begin, set currentTime directly if beginPTS buffered
            this._requestSetTime = true;
            this._mediaElement.currentTime = directSeekBeginTime;
        }  else if (directSeek) {  // buffered position
            if (!this._alwaysSeekKeyframe) {
                this._requestSetTime = true;
                this._mediaElement.currentTime = seconds;
            } else {
                let idr = this._msectl.getNearestKeyframe(Math.floor(seconds * 1000));
                this._requestSetTime = true;
                if (idr != null) {
                    this._mediaElement.currentTime = idr.dts / 1000;
                } else {
                    this._mediaElement.currentTime = seconds;
                }
            }
            if (this._progressChecker != null) {
                this._checkProgressAndResume();
            }
        } else {
            if (this._progressChecker != null) {
                window.clearInterval(this._progressChecker);
                this._progressChecker = null;
            }
            this._msectl.seek(seconds);
            this._transmuxer.seek(Math.floor(seconds * 1000));  // in milliseconds
            // no need to set mediaElement.currentTime if non-accurateSeek,
            // just wait for the recommend_seekpoint callback
            if (this._config.accurateSeek) {
                this._requestSetTime = true;
                this._mediaElement.currentTime = seconds;
            }
        }
    }

    _checkAndApplyUnbufferedSeekpoint() {
        if (this._seekpointRecord) {
            if (this._seekpointRecord.recordTime <= this._now() - 100) {
                let target = this._mediaElement.currentTime;
                this._seekpointRecord = null;
                if (!this._isTimepointBuffered(target)) {
                    if (this._progressChecker != null) {
                        window.clearTimeout(this._progressChecker);
                        this._progressChecker = null;
                    }
                    // .currentTime is consists with .buffered timestamp
                    // Chrome/Edge use DTS, while FireFox/Safari use PTS
                    this._msectl.seek(target);
                    this._transmuxer.seek(Math.floor(target * 1000));
                    // set currentTime if accurateSeek, or wait for recommend_seekpoint callback
                    if (this._config.accurateSeek) {
                        this._requestSetTime = true;
                        this._mediaElement.currentTime = target;
                    }
                }
            } else {
                window.setTimeout(this._checkAndApplyUnbufferedSeekpoint.bind(this), 50);
            }
        }
    }

    _checkAndResumeStuckPlayback(stalled) {
        let media = this._mediaElement;
        if (stalled || !this._receivedCanPlay || media.readyState < 2) {  // HAVE_CURRENT_DATA
            let buffered = media.buffered;
            if (buffered.length > 0 && media.currentTime < buffered.start(0)) {
                Log.w(this.TAG, `Playback seems stuck at ${media.currentTime}, seek to ${buffered.start(0)}`);
                this._requestSetTime = true;
                this._mediaElement.currentTime = buffered.start(0);
                this._mediaElement.removeEventListener('progress', this.e.onvProgress);
            }
        } else {
            // Playback didn't stuck, remove progress event listener
            this._mediaElement.removeEventListener('progress', this.e.onvProgress);
        }
    }

    _checkAndDoSeek() {
        let me = this._mediaElement;
        if (null != this._mediaElement && this._mediaElement.paused == false) {
            let b = me.buffered;
            if (b.length > 0) {
                let bEnd = 0;
                for (let i = 0; i < b.length; i++) {
                    let end = b.end(i);
                    if (bEnd < end) {
                        bEnd = end;
                    }
                }

                // Log.v(this.TAG, '_checkAndDoSeek, length: ' + b.length + ', currentTime: ' + me.currentTime +
                //     ', buffered end: ' + bEnd);

                if ((this._config.lowLatency == true && me.currentTime + 2 < bEnd) || (this._config.lowLatency == false && me.currentTime + 5 < bEnd)) {
                    Log.w(this.TAG, '_checkAndDoSeek, since currentTime ' + me.currentTime + ' is too later than buffered end ' + bEnd
                        + ', we need seek to catch the live point');
                    this._requestSetTime = true;
                    this._mediaElement.currentTime = (this._config.lowLatency == true) ? (bEnd - 1) : (bEnd - 2);
                    this._mediaElement.removeEventListener('progress', this.e.onvProgress);
                }
            }
        }
    }

    _checkBuffering() {
        let me = this._mediaElement;
        if (null != this._mediaElement) {
            let b = me.buffered;
            if (b.length > 0) {
                let bEnd = 0;
                for (let i = 0; i < b.length; i++) {
                    let start = b.start(i);
                    let end = b.end(i);
                    // Log.v(this.TAG, '_checkBuffering, buffered ' + i + '/' + b.length + ', start: ' + start + ', end: ' + end);
                    if (bEnd < end) {
                        bEnd = end;
                    }
                }

                // Log.v(this.TAG, '_checkBuffering, currentTime: ' + me.currentTime + ', buffered end: ' + bEnd + 
                //     ', diff: ' + ((bEnd > me.currentTime) ? bEnd - me.currentTime : 0) + ', paused: ' + this._mediaElement.paused);

                if(this._pausedSinceBuffering == false && this._mediaElement.paused == false) {
                    if(me.currentTime < bEnd) {
                        let playtimelen = bEnd-me.currentTime;
                        if(playtimelen > 4) {
                            me.playbackRate = 2;
                        } 
                        else if(playtimelen > 2) {
                            me.playbackRate = 1.2;
                        }
                        else if(bEnd-me.currentTime > 0.5){
                            me.playbackRate = 1;
                        } 
                        else {
                            me.playbackRate = 0.8;
                        }

                    }
                }

                if (this._pausedSinceBuffering == false && me.currentTime > bEnd && this._mediaElement.paused == false) {
                    Log.w(this.TAG, '_checkBuffering, since currentTime ' + me.currentTime + ' is beyond buffered end ' + bEnd
                        + ', we need pause to wait buffering');

                    this._pausedSinceBuffering = true;
                    this._mediaElement.pause();
                } else if (this._pausedSinceBuffering == true && me.currentTime + 2 < bEnd) {
                    Log.w(this.TAG, '_checkBuffering, since currentTime ' + me.currentTime + ' is later than buffered end ' + bEnd
                        + ', we need play');

                    this._pausedSinceBuffering = false;
                    this._mediaElement.play();
                }
            }
        }
    }

    _onvLoadedMetadata(e) {
        if (this._pendingSeekTime != null) {
            this._mediaElement.currentTime = this._pendingSeekTime;
            this._pendingSeekTime = null;
        }
    }

    _onvSeeking(e) {  // handle seeking request from browser's progress bar
        let target = this._mediaElement.currentTime;
        let buffered = this._mediaElement.buffered;

        if (this._requestSetTime) {
            this._requestSetTime = false;
            return;
        }

        if (target < 1.0 && buffered.length > 0) {
            // seek to video begin, set currentTime directly if beginPTS buffered
            let videoBeginTime = buffered.start(0);
            if ((videoBeginTime < 1.0 && target < videoBeginTime) || Browser.safari) {
                this._requestSetTime = true;
                // also workaround for Safari: Seek to 0 may cause video stuck, use 0.1 to avoid
                this._mediaElement.currentTime = Browser.safari ? 0.1 : videoBeginTime;
                return;
            }
        }

        if (this._isTimepointBuffered(target)) {
            if (this._alwaysSeekKeyframe) {
                let idr = this._msectl.getNearestKeyframe(Math.floor(target * 1000));
                if (idr != null) {
                    this._requestSetTime = true;
                    this._mediaElement.currentTime = idr.dts / 1000;
                }
            }
            if (this._progressChecker != null) {
                this._checkProgressAndResume();
            }
            return;
        }

        this._seekpointRecord = {
            seekPoint: target,
            recordTime: this._now()
        };
        window.setTimeout(this._checkAndApplyUnbufferedSeekpoint.bind(this), 50);
    }

    _onvCanPlay(e) {
        this._receivedCanPlay = true;
        this._mediaElement.removeEventListener('canplay', this.e.onvCanPlay);
    }

    _onvStalled(e) {
        Log.w(this.TAG, 'player stalled!');
        this._checkAndResumeStuckPlayback(true);
    }

    _onvProgress(e) {
        this._checkAndResumeStuckPlayback();
    }

}

export default MsePlayer;