import Transmuxer from '../core/transmuxer.js';
import TransmuxingEvents from '../core/transmuxing-events.js';
import {createDefaultConfig} from '../config.js';
import {InvalidArgumentException, IllegalStateException} from '../utils/exception.js';
import MsePlayer from './mse-player.js';
import WasmPlayer from './wasm-player.js';
import WasmMediaConst from '../common/ulu-media-const.js';
import ErrorDetails from './player-errors.js';

class FlvPlayer {

    constructor(mediaDataSource, config) {
        this.TAG = 'FlvPlayer';
        // this._type = 'FlvPlayer';

        this._config = createDefaultConfig();
        if (typeof config === 'object') {
            Object.assign(this._config, config);
        }

        if (mediaDataSource.type.toLowerCase() !== 'flv') {
            throw new InvalidArgumentException('FlvPlayer requires an flv MediaDataSource input!');
        }

        if (mediaDataSource.isLive === true) {
            this._config.isLive = true;
        }
        this._mediaDataSource = mediaDataSource;
        
        this._transmuxer = null;

        this._play_mode = 0;     //  1 - mse |  2 - wasm
        this._player_wasm = new WasmPlayer(this._config);
        this._player_mse = new MsePlayer(this._config);
        this._player = null;
        this._eventcb = null;
        this._playUI = null;
    }

    destroy() {
        this._config = null;
        this._mediaInfo = null;
        this._statisticsInfo = null;
        this._play_mode = 0;
        this._playUI.innerHTML = null;
        this._eventcb = null;

        this._destroy_transmuxer();
        this._destroy_player();
    }

    // may be video or canvas 
    play(playUI) {
        this._playUI = playUI;
        this._create_transmuxer();

        // if (this._player != null) {
        //     this._player.play(videoorcanvas);
        // }
    }

    pause() {
        if (this._player != null) {
            this._player.pause();
        }
    }

    resume() {
        if (this._player != null) {
            this._player.resume();
        }
    }

    stop() {
        if (this._player != null) {
            this._player.stop();
        }      
    }

    on_message(eventCB) {
        this._eventcb = eventCB;
    }

    _create_player() {
        // this._destroy_player();
        if(this._play_mode == 1) {
            this._player = this._player_mse;//new MsePlayer(this._config);
        }
        else if(this._play_mode == 2) {
            this._player = this._player_wasm;//new WasmPlayer(this._config);
        }

        if(this._player) {
            this._player.load(this._transmuxer, this._eventcb);
        }
    }

    _destroy_player() {
        if (this._player_wasm != null) {
            this._player_wasm.destroy();
            this._player_wasm = null; 
        }
        if (this._player_mse != null) {
            this._player_mse.destroy();
            this._player_mse = null; 
        }
        this._player = null;
    }

    _create_transmuxer() {
        this._destroy_transmuxer();
        this._transmuxer = new Transmuxer(this._mediaDataSource, this._config);
        this._transmuxer.on(TransmuxingEvents.INIT_SEGMENT, (type, is) => {
            if(this._play_mode == 0) {
                if(is.container.search('/raw') !== -1) {
                    this._play_mode = 2;
                    this._playUI.innerHTML = '<canvas id="uluvideo" width="720" height="500" ></canvas>';
                } else 
                {
                    this._play_mode = 1;
                    this._playUI.innerHTML = '<video id="uluvideo" width="100%" controls="controls" autoplay="autoplay" x5-playsinline="" playsinline="" webkit-playsinline=""></video>';
                }
                this._create_player();
                if(this._player != null) {
                    // var uluvideo = document.getElementById("uluvideo");
                    var uluvideo = this._playUI.children[0];
                    this._player.play(uluvideo);
                }
            }
            if (this._player != null) {
                return this._player.OnInitSegment(type, is);
            }
        });
        this._transmuxer.on(TransmuxingEvents.MEDIA_SEGMENT, (type, ms) => {
            if (this._player != null) {
                return this._player.OnMediaSegment(type, ms);
            }
        });
        this._transmuxer.on(TransmuxingEvents.LOADING_COMPLETE, () => {
            if (this._player != null) {
                return this._player.OnLoadComplete();
            } 
            else if(this._eventcb) {
                var e = {
                    e: WasmMediaConst.kErrorLoadComplete,
                    m: ErrorDetails.MEDIA_MSE_ERROR
                };
                return this._eventcb(e);
            }
        });
        this._transmuxer.on(TransmuxingEvents.RECOVERED_EARLY_EOF, () => {
            if (this._player) {
                return this._player.OnRecoverdEarlyEof();
            }
            else if(this._eventcb) {
                var e = {
                    e: WasmMediaConst.kErrorNetwork,
                    m: ErrorDetails.NETWORK_UNRECOVERABLE_EARLY_EOF
                };
                return this._eventcb(e);
            }
        });
        this._transmuxer.on(TransmuxingEvents.IO_ERROR, (detail, info) => {
            if (this._player) {
                return this._player.OnIoErr(detail, info);
            }
            else if(this._eventcb) {
                var e = {
                    e: WasmMediaConst.kErrorNetwork,
                    m: ErrorDetails.NETWORK_EXCEPTION
                };
                return this._eventcb(e);
            }
        });
        this._transmuxer.on(TransmuxingEvents.DEMUX_ERROR, (detail, info) => {
            if (this._player) {
                return this._player.OnDemuxErr(detail, info);
            }
            else if(this._eventcb) {
                var e = {
                    e: WasmMediaConst.kErrorMedia,
                    m: ErrorDetails.MEDIA_FORMAT_ERROR
                };
                return this._eventcb(e);
            }
        });
        this._transmuxer.on(TransmuxingEvents.MEDIA_INFO, (mediaInfo) => {
            if (this._player != null) {
                return this._player.OnMediaInfo(mediaInfo);
            }
        });
        this._transmuxer.on(TransmuxingEvents.STATISTICS_INFO, (statInfo) => {
            if (this._player != null) {
                return this._player.OnStatisticsInfo(statInfo);
            }
        });
        this._transmuxer.on(TransmuxingEvents.RECOMMEND_SEEKPOINT, (milliseconds) => {
            if (this._player != null) {
                return this._player.OnRecommendSeekPoint(milliseconds);
            }
        });
        this._transmuxer.open();
    }

    _destroy_transmuxer() {
        if (this._transmuxer) {
            this._transmuxer.close();
            this._transmuxer.destroy();
            this._transmuxer = null;
        }
    }

    full_screen() {
        if (this._player != null) {
            return this._player.fullscreen();
        }
        return null;
    }

    enable_audio(enable) {
        if (this._player != null) {
            return this._player.enableaudio(enable);
        }
        return null;
    }

    get type() {
        if (this._player != null) {
            return this._player.type();
        }
        return null;
    }

    get buffered() {
        if (this._player != null) {
            return this._player.buffered();
        }
        return null;
    }

    get duration() {
        if (this._player != null) {
            return this._player.duration();
        }
        return null;
    }

    get volume() {
        if (this._player != null) {
            return this._player.volume();
        }
        return null;
    }

    set volume(value) {
        if (this._player != null) {
            return this._player.volume(value);
        }
    }

    get muted() {
        if (this._player != null) {
            return this._player.muted();
        }
        return null;
    }

    set muted(muted) {
        if (this._player != null) {
            return this._player.muted(muted);
        }
    }

    get currentTime() {
        if (this._player != null) {
            return this._player.currentTime();
        }
        return null;
    }

    set currentTime(seconds) {
        if (this._player != null) {
            return this._player.currentTime(seconds);
        }
    }

    get mediaInfo() {
        if (this._player != null) {
            return this._player.mediaInfo();
        }
        return null;
    }

    get statisticsInfo() {
        if (this._player != null) {
            return this._player.statisticsInfo();
        }
        return null;
    }
}

export default FlvPlayer;