import Log from '../utils/logger.js';
import Browser from '../utils/browser.js';
import {IllegalStateException} from '../utils/exception.js';


// Fragmented mp4 remuxer
class RawRemuxer {

    constructor(config) {
        this.TAG = 'RawRemuxer';

        this._config = config;
        this._isLive = (config.isLive === true) ? true : false;

        // this._dtsBase = -1;
        this._dtsBaseInited = false;
        this._audioDtsBase = Infinity;
        this._videoDtsBase = Infinity;
        // this._audioNextDts = undefined;
        this._audioLastDts = undefined;
        this._videoLastDts = undefined;

        this._audioMeta = null;
        this._videoMeta = null;
        this._annextb = null;

        // this._audioSegmentInfoList = new MediaSegmentInfoList('audio');
        // this._videoSegmentInfoList = new MediaSegmentInfoList('video');

        this._onInitSegment = null;
        this._onMediaSegment = null;

        // Workaround for chrome < 50: Always force first sample as a Random Access Point in media segment
        // see https://bugs.chromium.org/p/chromium/issues/detail?id=229412
        this._forceFirstIDR = (Browser.chrome &&
                              (Browser.version.major < 50 ||
                              (Browser.version.major === 50 && Browser.version.build < 2661))) ? true : false;

        // Workaround for IE11/Edge: Fill silent aac frame after keyframe-seeking
        // Make audio beginDts equals with video beginDts, in order to fix seek freeze
        this._fillSilentAfterSeek = (Browser.msedge || Browser.msie);

        // While only FireFox supports 'audio/mp4, codecs="mp3"', use 'audio/mpeg' for chrome, safari, ...
        // this._mp3UseMpegAudio = !Browser.firefox;

        this._fillAudioTimestampGap = this._config.fixAudioTimestampGap;
    }

    destroy() {
        this._dtsBaseInited = false;
        this._audioMeta = null;
        this._videoMeta = null;
        // this._audioSegmentInfoList.clear();
        // this._audioSegmentInfoList = null;
        // this._videoSegmentInfoList.clear();
        // this._videoSegmentInfoList = null;
        this._onInitSegment = null;
        this._onMediaSegment = null;
    }

    bindDataSource(producer) {
        producer.onDataAvailable = this.remux.bind(this);
        producer.onTrackMetadata = this._onTrackMetadataReceived.bind(this);
        return this;
    }

    /* prototype: function onInitSegment(type: string, initSegment: ArrayBuffer): void
       InitSegment: {
           type: string,
           data: ArrayBuffer,
           codec: string,
       }
    */
    get onInitSegment() {
        return this._onInitSegment;
    }

    set onInitSegment(callback) {
        this._onInitSegment = callback;
    }

    /* prototype: function onMediaSegment(type: string, mediaSegment: MediaSegment): void
       MediaSegment: {
           type: string,
           data: ArrayBuffer,
           sampleCount: int32
       }
    */
    get onMediaSegment() {
        return this._onMediaSegment;
    }

    set onMediaSegment(callback) {
        this._onMediaSegment = callback;
    }

    insertDiscontinuity() {
        // this._audioNextDts = undefined;
        this._audioLastDts = this._videoLastDts = undefined;
    }

    seek(originalDts) {
        // this._videoSegmentInfoList.clear();
        // this._audioSegmentInfoList.clear();
    }

    remux(audioTrack, videoTrack) {
        if (!this._onMediaSegment) {
            throw new IllegalStateException('RawRemuxer: onMediaSegment callback must be specificed!');
        }
        if (!this._dtsBaseInited) {
            this._calculateDtsBase(audioTrack, videoTrack);
        }
        this._remuxVideo(videoTrack);
        this._remuxAudio(audioTrack);
    }

    _onTrackMetadataReceived(type, metadata) {
        if (type === 'audio') {
            this._audioMeta = metadata;
        } else if (type === 'video') {
            this._videoMeta = metadata; 
        } else {
            return;
        }

        let container = 'raw';
        // dispatch metabox (Initialization Segment)
        if (!this._onInitSegment) {
            throw new IllegalStateException('RawRemuxer: onInitSegment callback must be specified!');
        }
        this._onInitSegment(type, {
            type: type,
            codec: metadata.codec,
            container: `${type}/${container}`,
            metadata: metadata,
        });
    }

    _calculateDtsBase(audioTrack, videoTrack) {
        if (this._dtsBaseInited) {
            return;
        }

        if(this._audioDtsBase == Infinity) {
            if (audioTrack.samples && audioTrack.samples.length) {
                this._audioDtsBase = audioTrack.samples[0].dts;
            }
        }
        if(this._videoDtsBase == Infinity) {
            if (videoTrack.samples && videoTrack.samples.length) {
                this._videoDtsBase = videoTrack.samples[0].dts;
            }
        }

        if((this._audioMeta && this._audioDtsBase == Infinity) ||
        (this._videoMeta && this._videoDtsBase == Infinity)) {

        }else {
            this._dtsBaseInited = true;
            if(this._audioDtsBase !== Infinity && this._videoDtsBase !== Infinity) {
                if(Math.abs(this._audioDtsBase - this._videoDtsBase) < 200)
                {
                    let minVaule = Math.min(this._audioDtsBase, this._videoDtsBase);
                    this._audioDtsBase = minVaule;
                    this._videoDtsBase = minVaule;
                }
            }
        }

        Log.v(this.TAG, 'DTS initialized: (audio ' + this._audioDtsBase +
        ', video ' + this._videoDtsBase + ')');
    }

    _getSamplingFrequencyIndex(sampleRate) {
        var samplingRates = [96000, 88200, 64000, 48000, 44100, 32000, 24000, 22050, 16000, 12000, 11025, 8000, 7350];
        for (var i = 0; i < samplingRates.length; i++) {
            if (samplingRates[i] == sampleRate) {
                return i;
            }
        }
    
        return 4;   // return 44100 default
    }

    _remuxAudio(audioTrack) {
        if (this._audioMeta == null) {
            return;
        }

        let track = audioTrack;
        let samples = track.samples;
        let sampleLength = samples.length;
        // we will reserve the last sample for accurate sample duration
        if (!samples || sampleLength < 1) {
            return;
        }

        let firstDts = -1;
        let rawSamples = [];

        // Correct dts for each sample, and calculate sample duration. Then output to mp4Samples
        for (let i = 0; i < sampleLength; i++) {
            let sample = samples[i];
            
            let originalDts = sample.dts - this._audioDtsBase;
            let dts = originalDts;

            //let myDate = new Date();
            //Log.d(this.TAG, 'audio sample, dts: ' + dts + ', size: ' + sample.unit.byteLength + ', time: ' + myDate.getTime());

            if (this._audioLastDts) {
                if (dts <= this._audioLastDts) {
                    Log.d(this.TAG, 'audio dts rollback, we need re-adjust it, ' + dts + ', ' + this._audioLastDts);
                    dts = this._audioLastDts + 1;
                }
            }
            this._audioLastDts = dts;

            if (firstDts === -1) {
                firstDts = dts;
            }

            let sampleData = null;
            var sampleLen = sample.unit.byteLength;
            if (this._audioMeta.codec.search('mp4a') != -1) {
                let samplingFrequencyIndex = this._getSamplingFrequencyIndex(this._audioMeta.audioSampleRate);
                sampleLen += 7;
                sampleData = new Uint8Array(sampleLen);
                const ADTSHeader = new Uint8Array(7);
                ADTSHeader[0] = 0xff;
                ADTSHeader[1] = 0xf0;
                ADTSHeader[1] |= 0 << 3;
                ADTSHeader[1] |= 0 << 1;
                ADTSHeader[1] |= 1;
                ADTSHeader[2] = (2 - 1) << 6;
                ADTSHeader[2] |= (samplingFrequencyIndex & 0x0f) << 2;
                ADTSHeader[2] |= 0 << 1;
                ADTSHeader[2] |= (this._audioMeta.channelCount & 0x04) >> 2;
                ADTSHeader[3] = (this._audioMeta.channelCount & 0x03) << 6;
                ADTSHeader[3] |= 0 << 5;
                ADTSHeader[3] |= 0 << 4;
                ADTSHeader[3] |= 0 << 3;
                ADTSHeader[3] |= 0 << 2;
                ADTSHeader[3] |= (sampleLen & 0x1800) >> 11;
                ADTSHeader[4] = (sampleLen & 0x7f8) >> 3;
                ADTSHeader[5] = (sampleLen & 0x7) << 5;
                ADTSHeader[5] |= 0x1f;
                ADTSHeader[6] = 0xfc;
                sampleData.set(ADTSHeader,0);
                sampleData.set(sample.unit,7);
            }
            else {
                sampleData = new Uint8Array(sampleLen);
                sampleData.set(sample.unit, 0);
            }

            rawSamples.push({
                dts: dts,
                pts: dts,
                cts: 0,
                data: sampleData,
                size: sampleLen,
                originalDts: originalDts,
            });

            // Log.d(this.TAG, 'raw audio sample, dts: ' + dts + ', size: ' + sample.unit.byteLength + ', odts: ' + originalDts);
        }

        track.length = 0;
        track.samples.splice(0, sampleLength);

        this._onMediaSegment('audio', {
            codec: this._audioMeta.codec,
            samples: rawSamples,
            sampleCount: rawSamples.length,
        });
    }

    _remuxVideo(videoTrack) {
        if (this._videoMeta == null) {
            return;
        }

        let track = videoTrack;
        let samples = track.samples;
        let sampleLength = samples.length;
        let firstDts = -1;
        let firstPts = -1;

        // we will reserve the last sample for accurate sample duration
        if (!samples || sampleLength < 1) {
            return;
        }

        // let lastSample = samples[sampleLength - 1];
    
        let rawSamples = [];

        // Correct dts for each sample, and calculate sample duration. Then output to mp4Samples
        for (let i = 0; i < sampleLength; i++) {
            let sample = samples[i];
            let originalDts = sample.dts - this._videoDtsBase;
            let isKeyframe = sample.isKeyframe;
            let dts = originalDts;
            let cts = sample.cts;
            let pts = dts + cts;

            //let myDate = new Date();
            //Log.d(this.TAG, 'video sample, dts: ' + dts + ', size: ' + sample.length + ', time: ' + myDate.getTime());

            if (this._videoLastDts) {
                if (dts <= this._videoLastDts) {
                    Log.d(this.TAG, 'video dts rollback, we need re-adjust it, ' + dts + ', ' + this._videoLastDts);
                    let dtsOffset = this._videoLastDts + 1 - dts;
                    dts = this._videoLastDts + 1;
                    pts += dtsOffset;
                }
            }
            this._videoLastDts = dts;

            if (firstDts === -1) {
                firstDts = dts;
                firstPts = pts;
            }

            let units = sample.units;
            if(units.length == 1 && isKeyframe && this._videoMeta.annexb) {
                let sps_pps_len = this._videoMeta.annexb.byteLength;
                let sampleData = new Uint8Array(sample.length + sps_pps_len);
                sampleData.set(this._videoMeta.annexb, 0);

                let unit = units.shift();
                let data = unit.data;
                // convert AVC to H264, size -> start code (0 0 0 1)
                var unitLength = 0;
                if (data.byteLength >= 4) {
                    unitLength = (data[0] << 24) | (data[1] << 16) | (data[2] << 8) | data[3];
                }

                if (unitLength + 4 != data.byteLength) {
                    this.logger.logInfo("video data: " + data[0] + " " + data[1] + " " + data[2] + " " + data[3] + ", length: " + data.byteLength);
                } else {
                    data.set([0, 0, 0, 1]);
                }
                sampleData.set(data, sps_pps_len);
            }
            else{
                let sampleData = new Uint8Array(sample.length);
                let offset = 0;
                while (units.length) {
                    let unit = units.shift();
                    let data = unit.data;
                    // convert AVC to H264, size -> start code (0 0 0 1)
                    var unitLength = 0;
                    if (data.byteLength >= 4) {
                        unitLength = (data[0] << 24) | (data[1] << 16) | (data[2] << 8) | data[3];
                    }
    
                    if (unitLength + 4 != data.byteLength) {
                        this.logger.logInfo("video data: " + data[0] + " " + data[1] + " " + data[2] + " " + data[3] + ", length: " + data.byteLength);
                    } else {
                        data.set([0, 0, 0, 1]);
                    }
                
                    sampleData.set(data, offset);
                    offset += data.byteLength;
                }
            }
            // let nextDts = samples[i + 1].dts - this._dtsBase;
            // let sampleDuration = (nextDts > dts) ? (nextDts - dts) : 1;

            rawSamples.push({
                dts: dts,
                pts: pts,
                cts: cts,
                data: sampleData,
                size: sample.length,
                isKeyframe: isKeyframe,
                // duration: sampleDuration,
                originalDts: originalDts,
            });

            // if(isKeyframe) {
            //     Log.d(this.TAG, 'raw video sample, dts: ' + dts + ', pts: ' + pts + ', cts: ' + cts + ', size: ' + sample.length + ', odts: ' + originalDts);
            // }
        }

        track.length = 0;
        track.samples.splice(0, sampleLength);

        this._onMediaSegment('video', {
            codec: this._videoMeta.codec,
            samples: rawSamples,
            sampleCount: rawSamples.length,
        });
    }

    _parseConfig() {
        if(this._videoMeta.codec.search('h265') != -1) {

        } else {

        }
    }
}

export default RawRemuxer;