

class uluMediaRefClock {

    constructor() {
        this.playingTime=0;
        this.lastVideoRenderTime=0;
        this.updateSystemTime=0;
        this.recordMediaTime=0;
        this.recordSystemTime=0;
        this.playSpeed=1.0;
        this.videoRenderNum = 0;
        this.videoAdjustTime = 200; // 200ms
    }

    updateByVideoRender(videoRenderTime) {
        this.updateSystemTime = 0;
        if (videoRenderTime< 5000&& videoRenderTime+5000<this.lastVideoRenderTime){
            this.recordMediaTime=0;
            this.recordSystemTime=0;
        }
        this.lastVideoRenderTime = videoRenderTime;
        this.videoRenderNum++;
    }

    isVideoRenderStart(){
        return this.videoRenderNum;
    }

    setSpeed(speed) {
        this.playSpeed = speed;
    }

    getSpeed() {
        return this.playSpeed;
    }

    forceUpdateByVideo(videoRenderTime) {

        this.updateByVideoRender=0;
        this.updateSystemTime = 0;
        this.recordMediaTime = 0;
        this.recordSystemTime=0;
        this.lastVideoRenderTime = videoRenderTime;
    
    }

    getLastRenderMediaTime(){
        return this.lastVideoRenderTime;
    }

    getPlaytingTime() {
        if (this.lastVideoRenderTime <= 0) {
            return 0;
        }
     
        let nowTime = Date.now();
        if (this.updateSystemTime === 0) {
            this.updateSystemTime = nowTime;
        }
     
        if (this.recordSystemTime === 0) {
            this.recordSystemTime = this.updateSystemTime;
            this.recordMediaTime = this.lastVideoRenderTime;
        }
     
        {
             let videoRenderIncrement = (this.lastVideoRenderTime > this.recordMediaTime) ? (this.lastVideoRenderTime - this.recordMediaTime) : 0;
             let systemTimeIncrement = (this.updateSystemTime - this.recordSystemTime) * this.playSpeed;
             if (videoRenderIncrement > systemTimeIncrement + this.videoAdjustTime) {
                 this.recordMediaTime = 0;
                 this.recordSystemTime = 0;
                 this.playingTime = this.lastVideoRenderTime + (nowTime - this.updateSystemTime) * this.playSpeed;
             } else if (videoRenderIncrement + this.videoAdjustTime < systemTimeIncrement) {
                 this.recordMediaTime = 0;
                 this.recordSystemTime = 0;
                 this.playingTime = this.lastVideoRenderTime + (nowTime - this.updateSystemTime) * this.playSpeed;
             }else{
                 this.playingTime = (nowTime - this.recordSystemTime) * this.playSpeed +this.recordMediaTime;
             }
        }
        return this.playingTime;
     
    }
}

export default uluMediaRefClock;